/* inter-100 - cyrillic_latin */
@font-face {
  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
  font-family: 'Inter';
  font-style: normal;
  font-weight: 100;
  src: url('../fonts/inter-v12-cyrillic_latin-100.eot'); /* IE9 Compat Modes */
  src: url('../fonts/inter-v12-cyrillic_latin-100.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/inter-v12-cyrillic_latin-100.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/inter-v12-cyrillic_latin-100.woff') format('woff'), /* Modern Browsers */
       url('../fonts/inter-v12-cyrillic_latin-100.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/inter-v12-cyrillic_latin-100.svg#Inter') format('svg'); /* Legacy iOS */
}
